/* font-family: 'Roboto Slab', serif; 400,500, 600, 700, 800, 900 */
/* font-family: 'Noto Sans JP', sans-serif; 100, 300, 400, 500, 700, 900 */
/* General */
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  /* background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%); */
  background-image: linear-gradient(to top, rgba(72, 197, 239, 0.467) 0%, rgba(111, 133, 214, 0.446) 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

a, li {
  text-decoration: none; }

h1, h2, h3, h4 {
  font-family: 'Roboto Slab', serif; }

h1 {
  font-weight: 900;
  font-size: 4rem; }

h2 {
  font-weight: 700;
  font-size: 3.6rem; }

h3 {
  font-weight: 500;
  font-size: 3rem; }

h4 {
  font-weight: 400;
  font-size: 2.6rem; }

p, span, a, li {
  font-family: 'Noto Sans JP', sans-serif; }

p, li {
  font-weight: 300; }

span {
  font-weight: 100; }

a {
  font-weight: 400; }

h1, h2, h3, h4, p, span, a {
  margin: 0; }

img {
  width: 100%; }

/* Variables */
/* Utilities */
.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto; }

/* Display */
.flex {
  display: flex; }

.grid {
  display: grid; }

.none {
  display: none; }

.grid-gap {
  column-gap: 2rem; }

/* Align text */
.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-just {
  text-align: justify; }

/* Bóton */
.btn {
  border: none;
  text-decoration: none;
  padding-top: 1rem 2rem;
  text-align: center; }

.sombra {
  border: 0.1rem solid #CCCCCC;
  border-radius: 1rem;
  box-shadow: 0.5rem 0.5rem 0.5rem #CCCC; }

/* Mixxins */
/* Section */
/* Header */
.header {
  padding: 1rem;
  max-height: 5rem; }
  .header .header-contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); }
    @media (min-width: 768px) {
      .header .header-contenido {
        display: grid;
        grid-template-columns: 40% 60%; } }

.header-img {
  margin: 0 auto; }
  .header-img img {
    max-height: 5rem;
    max-width: 5rem; }

.menu-movil {
  display: flex;
  flex-direction: column; }
  .menu-movil span {
    background: #FFFFFF;
    width: 3rem;
    height: 0.5rem;
    margin: 0.3rem auto; }
  @media (min-width: 768px) {
    .menu-movil {
      display: none; } }

.nave {
  background-image: linear-gradient(to top, #0c368f 0%, #330867 100%);
  padding: 2rem 4rem;
  display: block;
  width: 200%;
  border: 0.1rem solid #FFFFFF;
  border-radius: 1rem;
  overflow: hidden;
  z-index: 9; }
  .nave a {
    font-size: 2.5rem;
    margin: 3rem auto;
    display: block;
    text-align: center;
    color: #FFFFFF; }
    .nave a:hover {
      color: #77f1fc; }
  @media (min-width: 768px) {
    .nave {
      width: 100%;
      border: none;
      display: flex;
      justify-content: space-between;
      padding: 0; }
      .nave a {
        padding: 0;
        display: inline; } }

/* Sobre */
.sobre {
  padding: 2rem;
  background: #FFFFFF;
  width: 90%;
  margin: 6rem auto 0 auto; }

.sobre-content {
  padding: 2rem 0; }
  @media (min-width: 768px) {
    .sobre-content {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(2, 1fr); } }

.sobre-img {
  width: 80%;
  height: 28rem;
  margin: 0 auto;
  background: url("../img/me.webp");
  background-size: cover;
  background-position: center; }

.sobre-info {
  padding: 2rem 0; }
  .sobre-info p {
    margin: 1rem auto;
    font-size: 1.5rem;
    font-weight: 700; }

/* Skills */
.skills {
  padding: 2rem;
  background: #e4f8ff;
  width: 90%;
  margin: 0 auto; }

.listas {
  padding: 2rem 0;
  width: 100%; }
  .listas h4 {
    padding: 1rem 0; }
  .listas ul {
    margin: 0 auto; }
    @media (min-width: 768px) {
      .listas ul {
        width: 40%; } }
    .listas ul li {
      font-size: 1.5rem;
      padding: 0.5rem 0; }

/* expirence */
.expirence {
  padding: 2rem;
  background: #FFFFFF;
  width: 90%;
  margin: 0 auto; }

@media (min-width: 768px) {
  .exprence-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

.place p {
  font-size: 2rem;
  font-weight: 400; }

.place ul {
  font-size: 1.6rem; }

.place li {
  padding: 0.5rem 0;
  text-align: justify; }

/* Briefcase */
.briefcase {
  padding: 2rem;
  background: #e4f8ff;
  width: 90%;
  margin: 0 auto; }
  .briefcase p {
    font-size: 2rem; }

.briefcase-content {
  margin: 2rem auto;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .briefcase-content {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(4, 1fr); } }

.item p {
  margin: 0.5rem auto;
  font-size: 1.8rem; }

/* Footer */
.footer {
  padding: 2rem 0;
  background: #000000;
  color: #FFFFFF; }

.footer-content p {
  padding: 1rem 0;
  font-size: 1.5rem; }
  .footer-content p span a {
    color: #FFFFFF;
    font-weight: 400; }

@media (min-width: 768px) {
  .footer-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }
